@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  letter-spacing: .5px;
  overflow: auto;
  margin: 0;
  padding: 0;
  background: lightgrey;
  margin-top: 10px;
}

body {
  margin: 0;
  color: black;
}

#root {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  position: relative;
  height: 100vh;
  padding: 0 20px
}

@media (-webkit-min-device-pixel-ratio: 1.1) and (-webkit-max-device-pixel-ratio: 1.99) {
  body {
    zoom: 0.67;
  }

}
.header {
    display: flex;
    flex-direction: row;
    margin: 0;
    height: 100px;

    align-items: center;
    justify-content: center;
    min-height:100px;
}


.menu-item {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: beige;
    padding: 0 15px;
   
}

.menu-item a {
    text-decoration: none;
    font-size: 1.2rem;
    color: black;
}

.menu-item a:hover {
    color: orange;
   
}
.menu-bar {
  display: flex;
  flex-direction: row;
  align-content: space-between;
  margin-left: auto;
  align-items: center;
  justify-content: center;
}

.menu-bar div+div {
  border-left: solid 1px black;
}

@media screen and (max-width: 875px) {
  .menu-bar {
    position: fixed;
    left: -100%;
    top: 5rem;
    flex-direction: column;
    background-color: #fff;
    width: 100%;
    border-radius: 10px;
    text-align: center;
    transition: 0.3s;
    box-shadow:
      0 10px 27px rgba(0, 0, 0, 0.05);
  }

  .menu-bar div+div {
    border: 0px;
  }
  .menu-item {
    height: 50px;
  }
  .menu-bar.active {
    left: 0;
  }

  .hamburger.active .bar:nth-child(2) {
    opacity: 0;
  }

  .hamburger.active .bar:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }

  .hamburger.active .bar:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }

}
.icon {
    display: inline-block;
    width: 25px;
    height: 25px;
    background-color: orange;
    border-radius: 15px;
}
.name {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0 20px;
}
.designation {
    padding-left: 20px;
} 
.full-name-link{
    text-decoration: none;
}

@media screen and (max-width: 490px) {
    .name {
        flex-direction: column;
        align-items: flex-start;
    }
    .designation {
        padding-left: 0px; 
    } 
  }

.hamburger {
    display: none;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
    background-color: #101010;
}


@media screen and (max-width: 875px) {
    .hamburger {
        display: block;
        margin-left: auto;
        cursor: pointer;
    }
  }

.footer {
    display: flex;
    margin-top: auto;
    min-height:50px;
    justify-content: space-between;
    height: 10vh;
    
}

.social-media-channels {
    display: flex;
    justify-content: space-evenly;
}

.footer-item-heading {
    font-weight: bold;
    font-size: 1.1rem;
    height: 30px;
}

@media screen and (max-width: 575px) {
    .footer {
      flex-direction: column;
      padding-top: 50px;
    }

    .footer-item{
        padding-bottom: 20px;
    }

    .social-media-channels {
        justify-content: flex-start;
    }
  }

.homepage-body__image{
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.homepage-body__image img {
    height: 30vw;
    padding-top: 50px;
    min-height: 250px;
}


.homepage-body__desc {
    width: 45vw;
    display: flex;
    flex-direction: column;
    letter-spacing: 1px;
}

.homepage-body__desc h1 {
    font-family: 'Homemade Apple', cursive;
    font-size: 3rem;
    color: gray;
}

.homepage-body__desc span {
    color: gray;
    font-size: 2rem;
}

.homepage-body__desc p {
    line-height: 1.6em;
    font-size: 1.2rem;
}

.homepage-body__desc__nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.homepage-body__desc__nav__item {
    width: 140px;
    height: 140px;
    border: 1px solid black;
    border-radius: 80px;
    text-align: center;
    position: relative;

}

.homepage-body__desc__nav__item a {
    width: 100%;
    text-decoration: none;
    color: black;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.6rem;
}

.homepage-body__desc__nav__item a:hover {
    color: orange;
}

#resume {
    background-color: orange;
}

#projects {
    background-color: red;
}

#contact {
    background-color:rgb(128, 216, 218);
}

#resume:hover,
#projects:hover,
#contact:hover {
    background-color: white;
}


@media screen and (max-width: 1024px) {
    .homepage-body__desc__nav__item {
        width: 10vw;
        height: 10vw;
        min-width: 80px;
        min-height: 80px;
    }
    .homepage-body__desc p {
        line-height: 1.6em;
        font-size: 1rem;
    }

    .homepage-body__desc__nav__item a {
        font-size: 1rem;
    }

    .homepage-body__desc h1{
        font-size: 2.5rem;
    }
  }

  @media screen and (max-width: 575px) {
    .homepage-body__desc {
      width: auto;
      
    }
  }
.homepage-body {
    display: flex;
    justify-content: space-evenly;
}

@media screen and (max-width: 575px) {
    .homepage-body {
      flex-direction: column-reverse;
      
    }
  }
