.footer {
    display: flex;
    margin-top: auto;
    min-height:50px;
    justify-content: space-between;
    height: 10vh;
    
}

.social-media-channels {
    display: flex;
    justify-content: space-evenly;
}

.footer-item-heading {
    font-weight: bold;
    font-size: 1.1rem;
    height: 30px;
}

@media screen and (max-width: 575px) {
    .footer {
      flex-direction: column;
      padding-top: 50px;
    }

    .footer-item{
        padding-bottom: 20px;
    }

    .social-media-channels {
        justify-content: flex-start;
    }
  }
