.homepage-body__desc {
    width: 45vw;
    display: flex;
    flex-direction: column;
    letter-spacing: 1px;
}

.homepage-body__desc h1 {
    font-family: 'Homemade Apple', cursive;
    font-size: 3rem;
    color: gray;
}

.homepage-body__desc span {
    color: gray;
    font-size: 2rem;
}

.homepage-body__desc p {
    line-height: 1.6em;
    font-size: 1.2rem;
}

.homepage-body__desc__nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.homepage-body__desc__nav__item {
    width: 140px;
    height: 140px;
    border: 1px solid black;
    border-radius: 80px;
    text-align: center;
    position: relative;

}

.homepage-body__desc__nav__item a {
    width: 100%;
    text-decoration: none;
    color: black;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    font-size: 1.6rem;
}

.homepage-body__desc__nav__item a:hover {
    color: orange;
}

#resume {
    background-color: orange;
}

#projects {
    background-color: red;
}

#contact {
    background-color:rgb(128, 216, 218);
}

#resume:hover,
#projects:hover,
#contact:hover {
    background-color: white;
}


@media screen and (max-width: 1024px) {
    .homepage-body__desc__nav__item {
        width: 10vw;
        height: 10vw;
        min-width: 80px;
        min-height: 80px;
    }
    .homepage-body__desc p {
        line-height: 1.6em;
        font-size: 1rem;
    }

    .homepage-body__desc__nav__item a {
        font-size: 1rem;
    }

    .homepage-body__desc h1{
        font-size: 2.5rem;
    }
  }

  @media screen and (max-width: 575px) {
    .homepage-body__desc {
      width: auto;
      
    }
  }