.name {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0 20px;
}
.designation {
    padding-left: 20px;
} 
.full-name-link{
    text-decoration: none;
}

@media screen and (max-width: 490px) {
    .name {
        flex-direction: column;
        align-items: flex-start;
    }
    .designation {
        padding-left: 0px; 
    } 
  }
